/* ==========================================================================
   Product List - Carousel shortcode responsive styles - begin
   ========================================================================== */
@include ipad-landscape{
    .eltdf-plc-holder{
        .eltdf-owl-slider{
            .owl-next{
                right: -72px;
            }
            .owl-prev{
                left: -72px;
            }
        }
    }

}

@include ipad-portrait {
    
    .eltdf-plc-holder {

        .eltdf-owl-slider{
            .owl-next{
                right: -52px;
            }
            .owl-prev{
                left: -52px;
            }
        }

        &.eltdf-simple-type {
        
            .eltdf-plc-image-outer,
            .eltdf-plc-text-wrapper {
                display: inline-block;
                width: 100%;
                padding: 0;
            }

            .eltdf-plc-text-wrapper {
                margin: 30px 0 40px;
            }
        }
    }
}

@include phone-landscape{
    .eltdf-plc-holder {

        .eltdf-owl-slider{

            .owl-nav {
                display: none;
                .owl-next {
                    right: -32px;
                }
                .owl-prev {
                    left: -32px;
                }
            }
        }
    }
}
/* ==========================================================================
   Product List - Carousel shortcode responsive styles - end
   ========================================================================== */